@import url('https://fonts.googleapis.com/css2?family=Press Start 2P&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');
body {
  min-height: 100vh;
  margin: 0px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;

  background-color: silver;
}
.spacing{
  padding: 3vw;
}
main
{
  width: 90%;
  margin: auto;
  justify-content: center;
  align-items: center;
  align-self: center;

}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
}
h1 {
  max-height: 100%;
  max-width: 100%;
  margin: 0;
  padding: 2vw;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
  color: white;
  -webkit-text-stroke: 0.05vw black;
  text-shadow: 0.4vw 0.4vw #101921;
  font-size: 7vw;
  border: 0;
  align-self: center;
  justify-content: space-evenly;
  text-align: center;
  font-family: 'Press Start 2P', sans-serif;
}
.h2 {
  max-height: 100%;
  max-width: 80%;
  margin: 10px;
  padding: 2vw!important;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
  text-align: center;
  color: white;
  -webkit-text-stroke: 0.025vw white;
  box-shadow: 0 0vw 0.5vw white;
  background: rgb(192, 43, 43);
 
  text-shadow: 0.2vw 0.2vw #101921;
  font-size: 30px;
  border: 0;
  align-self: center;
  justify-content: space-evenly;
  font-family: 'Press Start 2P', sans-serif;
}
h3{
  max-height: 100%;
  max-width: 100%;
  margin: 2vw;
  padding: 2vw!important;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
  text-align: center;
  color: white;
  -webkit-text-stroke: 0.02w black;
  text-shadow: 0.2vw 0.2vw #101921;
  
  font-size: 1.3vw;
  border: 0;
  align-self: center;
  justify-content: space-evenly;
  font-family: 'Press Start 2P', sans-serif;
}
h4{
  max-height: 100%;
  max-width: 100%;
  margin: 2vw;
  padding: 2vw!important;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
  text-align: center;
  color: white;
  -webkit-text-stroke: 0.02w black;
  text-shadow: 0.2vw 0.2vw #101921;
  font-size: 1.3vw;
  border: 0;
  align-self: center;
  justify-content: space-evenly;
  font-family: 'Press Start 2P', sans-serif;
}
p{
  padding: 10px;
  color: rgb(36, 32, 32);
  font-weight: 600;
  background: white;
  width: fit-content;
  min-width: 300px;
  display: flex;
  border-radius: 5px;
 margin-left: auto;
 margin-right: auto;
  font-size: 16px;
  border: 0;
  font-family: 'Press Start 2P', sans-serif;
}

.button {
  font-size: 1.9vw;
  padding: 5px;
  margin: 10px;
  display: flex;
  font-family: 'Press Start 2P', sans-serif;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: rgba(255, 255, 255, 0.8);
  background: red;
  transition: all 0.2s ease-in-out;
  box-shadow: 0 0 0.5vw white;
}

.glow-button:hover {
  color: white;
  font-size: 1.85vw;
  box-shadow: 0 0.5vw 1.5vw black;
}
.video-button:hover {
  max-width: 39.5%;
  max-height: 39.5%;
  color: white;
  font-size: 1.85vw;
  box-shadow: 0 0.5vw 1.5vw black;
}
.points{
  background-color: rgb(192, 43, 43);
 padding: 1px 10px;
 border-radius: 5px;
  width:fit-content;
  box-shadow: 0 0.5vw 1.5vw black;
  margin-left: auto;
  margin-right: auto;
}
.one{
flex:0.4;
}
.two{
  flex:0.1;
  }
  .three{
    flex:0.5;
    }

.b1 {
  width: 20vw;
  height: 5vw;
}
.b2 {
  width: 20vw;
  height: 5vw;
}
.align-right {
  justify-content: right;
}

.mint-bn{
  font-size: 16px !important;
  margin-top:50px!important; 
  padding-left: 0px !important;
  padding-right: 0px !important;
  margin: 10px !important;
  display: flex !important;
  font-family: 'Press Start 2P', sans-serif!important;
  justify-content: center !important;
  align-items: center !important;
  flex-direction: column !important;
  color: white !important;
  background: rgb(192, 43, 43)!important;
  transition: all 0.2s ease-in-out !important;
  
  border-radius: 0px !important;

}
.glow-button-2:hover {
  
  color: white;
  font-size: 1.01vw;
  box-shadow: 0 0.5vw 1.5vw black;
}

.image-gif{
  max-height: 300px;
  max-width: 300px;
  margin:0.51vw;
  padding: 0.5vw!important;
 
  padding: 0px;
  text-align: center;
  
  -webkit-text-stroke: 0.02w black;
  
  
  border: 0;
  font-family: 'Press Start 2P', sans-serif;
  box-shadow: 0 0.5vw 1.5vw black;
}
.MuiTypography-root
{
color:white!important;
}
.MuiButton-label
{
color:white!important;
}
@media(max-width:500px){
h2{
  font-size: 20px!important;
}
.image-gif{
  max-width: 200px;
  max-height: 200px;
  margin-top: 50px;
}
p{
  min-width: 250px;
}
.points{
  margin-top:40px
}
}